// @ts-check
import { hasVideoGroup, findEls, createLabel } from '../utils';
import { recessionClassName } from './recessions.constants';
import {
  getRecessionTextAndCSS,
  calculateStartX,
  getRecessionLabelPadding,
} from './recessions.utils';
/**
 * Appends apropriate recession label
 * and calculates start position based on the suffix label width if present
 * @param {Object} chart - highcharts chart object
 * @param {ChartlosOptions} options - chartlos options object
 */
export const onLoad = (chart, options) => {
  const { renderer, xAxis } = chart;
  const { axes, id, product } = options;
  const { showRecessions, showUkRecessions } = axes;
  const recessionGroup = xAxis[0].plotLinesAndBandsGroups['bands-0'];
  const needRecessions = recessionGroup && (showRecessions || showUkRecessions);
  if (!needRecessions) return;
  const plotBands = findEls(recessionGroup, 'path');
  const [recessionText, css] = getRecessionTextAndCSS(options);
  const isVideoGroup = hasVideoGroup(id);
  const recessionLabelY = chart.plotTop - getRecessionLabelPadding(isVideoGroup, product);
  /**
   * draw recession label
   * add recessionLabel property to Highcharts.Chart
   */
  // eslint-disable-next-line no-param-reassign
  chart.recessionLabel = createLabel(
    renderer,
    recessionText,
    0,
    recessionLabelY,
    recessionClassName,
    css
  );
  const startX = calculateStartX(chart, plotBands);
  chart.recessionLabel.element.setAttribute('x', startX);
};
