// @ts-check
import { RECESSIONS, UK_RECESSIONS } from './recessions.constants';

/**
 * Pre Render
 */
export function preRender(data, options) {
  if (canShowRecessions(options)) {
    const values = convertDataToValues(options.axes.showRecessions, options.axes.showUkRecessions);
    const dateA = options.dates[0];
    const dateB = options.dates[options.dates.length - 1];
    const axisValueA = new Date(dateA.year, dateA.month || 0).valueOf();
    const axisValueB = new Date(dateB.year, dateB.month || 0, dateB.date || 1).valueOf();

    let plotBands = values
      .map((v) => {
        if (v.start <= axisValueB && v.end >= axisValueA) {
          return {
            from: Math.max(axisValueA, v.start),
            to: Math.min(axisValueB, v.end),
            className: 'recession',
          };
        }
        return null;
      })
      .filter((item) => item !== null);

    if (plotBands.length) {
      if (options.quarterlyResults && options.series[0].data.length <= 44) {
        plotBands = convertDatetimeToLinear(plotBands, options.dates);
      }
      // eslint-disable-next-line no-param-reassign
      data.xAxis.plotBands = [...data.xAxis.plotBands, ...plotBands];
    }
  }
  return data;
}

function canShowRecessions(options) {
  return (
    (options.axes.showRecessions || options.axes.showUkRecessions) &&
    options.dates &&
    options.type !== 'bar' &&
    allHaveYears(options.dates)
  );
}

function allHaveYears(dates) {
  return !dates.some((date) => date.year === undefined);
}

function convertDataToValues(showRecessions, showUkRecessions) {
  if (showRecessions) return convertDataToValuesHelper(RECESSIONS);
  if (showUkRecessions) return convertDataToValuesHelper(UK_RECESSIONS);
}

function convertDataToValuesHelper(recessionsDates) {
  return recessionsDates.map((r) => ({
    start: new Date(r.start.year, r.start.month).valueOf(),
    end: r.end ? new Date(r.end.year, r.end.month).valueOf() : new Date().valueOf(),
  }));
}

// On datetime axes, values are milliseconds. On linear axes, values are
// just indices. For quarterly results, the axis is actually linear and just
// pretends to be datetime, so plotBands must be converted from datetime.
function convertDatetimeToLinear(plotBands, dates) {
  const datesLen = dates.length;
  plotBands.forEach((pb) => {
    let newFrom = 0;
    let newTo = datesLen - 1;
    dates.forEach((date, index) => {
      if (pb.from >= date.value) {
        newFrom = index;
      }
      if (pb.to >= date.value) {
        newTo = index;
      }
    });
    newFrom = getLinearPrecise(newFrom, pb.from, dates);
    newTo = getLinearPrecise(newTo, pb.to, dates);
    pb.from = newFrom;
    pb.to = newTo;
  });
  return plotBands;
}

// For use with convertDatetimeToLinear().
function getLinearPrecise(linear, datetime, dates) {
  if (linear < dates.length - 1) {
    linear += (datetime - dates[linear].value) / (dates[linear + 1].value - dates[linear].value);
  }
  return linear;
}
