// @ts-check
import '../../typedefs';
import {
  barronsRecessionLabelText,
  recessionLabelText,
  chineseRecessionLabel,
  japaneseRecessionLabel,
} from './recessions.constants';
import { findEls } from '../utils';

/**
 * Recession text based on publication and CSS object
 * @param {ChartlosOptions['product']} product - publication
 * @param {ChartlosOptions} options - chartlosOptions
 * @returns {string|Object[]} an array of text and css object
 */
function getRecessionTextAndCSS(options) {
  const { product, headings, styles } = options;
  const css = { zIndex: 1 };
  if (headings.chartFont === 'en' || !headings.chartFont) {
    return [product === 'barrons' ? barronsRecessionLabelText : recessionLabelText, css];
  }
  const [fontFamily, labelText] =
    headings.chartFont === 'ch'
      ? [styles.chFontFamily, chineseRecessionLabel]
      : [styles.jaFontFamily, japaneseRecessionLabel];
  css['font-family'] = fontFamily;
  return [labelText, css];
}

/**
 * End x of top y-axis label
 * if suffix is present calculates end x of the suffix
 * @param {Highcharts.Chart.suffix} suffix
 * @param {Highcharts.Chart.yAxis} yAxis
 * @returns {number}
 */
const getXLabelEndX = (suffix, yAxis) => {
  if (suffix) {
    const yLabelWithSuffix = suffix.element;
    return +yLabelWithSuffix.getAttribute('x') + yLabelWithSuffix.getBBox().width;
  }
  const yLabelX = findEls(yAxis.labelGroup, 'text');
  return +yLabelX[yLabelX.length - 1].getAttribute('x');
};

/**
 * Calculates start x position for recession label
 * @param {Highcharts.Chart} chart - highcharts chart object
 * @param {NodeList} plotBands - recession plot bands
 * @returns Start x position of the recession label
 */
function calculateStartX(chart, plotBands) {
  // @ts-ignore
  const { recessionLabel, suffix, plotWidth, plotLeft, yAxis } = chart;

  /**
   * @type {number} - labelWidth - recession label width in px
   */
  const labelWidth = recessionLabel.getBBox().width;
  const chartEndX = plotWidth + plotLeft;
  const yAxisLabelEndX = getXLabelEndX(suffix, yAxis[0]);

  for (const plotBand of plotBands) {
    const plotBandDArr = plotBand.getAttribute('d').split(' ');
    /**
     * @type {number} - startX - plot band start x position
     */
    const startX = +plotBandDArr[1];
    const endX = startX + labelWidth;
    // if recession label longer than chart width, start before startX
    if (endX > chartEndX) return chartEndX - labelWidth;
    // if suffix doesn't overlap don't do anything
    if (yAxisLabelEndX < startX) return startX;
    if (plotBands.length === 1) {
      const plotBandEndX = +plotBandDArr[7];
      if (plotBandEndX - labelWidth < startX && plotBandEndX - labelWidth > yAxisLabelEndX)
        return plotBandEndX - labelWidth;
      if (plotBandEndX - labelWidth > startX && startX > yAxisLabelEndX) return startX;
      return yAxisLabelEndX + 20;
    }
  }
}

/**
 * Padding bottom for recession label.
 * Should go inline with to x-axis label
 * @param {boolean} isVideoGroup - whether chart is video
 * @param {string} product - publication
 * @returns padding
 */
function getRecessionLabelPadding(isVideoGroup, product) {
  switch (product) {
    case 'barrons':
      return 5;
    default:
      return isVideoGroup ? 10 : 2;
  }
}

export { calculateStartX, getRecessionTextAndCSS, getRecessionLabelPadding };
